body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: url(/project_2/static/media/img15.3e5fe848.jpg);
  background-repeat: no-repeat;
  background-size: 100% 100%;

}



.App-header {
  min-height: 40vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(0, 0, 0);
  font-style: oblique;
  border: 10px solid bisque;
  padding-top: 4%;
}
.App-header h2{
  color: white;
  text-shadow: 2px 2px 2px rgba(0, 0, 0, 0.041), 0 0 20px rgb(155, 86, 8), 0 0 5px rgb(139, 90, 0);
font-family: URW Chancery L, cursive;
font-weight: bold;
}

.book-library{
  border: 10px solid bisque;
  padding-top: 4%;
}
.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.button {
  background-color: #c96a1cc4;
  border: none;
  color: white;
  padding: 20px 34px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 20px;
  margin: 4px 2px;
  cursor: pointer;
  margin-bottom: 10%;
  border-radius: 10px;
  }

  .btnSearch{
    background-color: #c96a1cc4;
    border: none;
    color: white;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 20px;
    cursor: pointer;
    border-radius: 10px;

  }
  input {
    text-align: center;
    }
  footer{
    min-height: 14vh;
    padding: 3%;
    background-color: #c96a1cc4;

  }
